body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
    font-family: 'Mulish', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
  display: flex;
  background-color: rgb(49, 37, 37);
  color: white;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 550px;
  width:400px;
  border-radius: 20px;
  box-shadow: 0px 0px 8px 2px white inset;
  /* margin-left: 320px; */
  flex-direction: column;
  
}
.cont{
  display: flex;
  align-items: center;
  /* justify-content: center; */
  height: 100vh;
  width:100vw;
}
.App button{
  height: 50px;
  width: 50px;
  font-size: large;
  color:white;
  cursor: pointer;
}
.forward,.backward{
  background-color: rgb(49, 37, 37);
  border: none;
}
.play{
  border-radius: 50%;
  background-color:rgb(112, 112, 168);
  border:none;
}
.App h1,h2,h3,h4,h5,p{
  text-shadow: 2px 2px 8px #ff0000;
}
.App img{
  height: 180px;
  width: 200px;
  border-radius: 50%;
  box-shadow: 0px 0px 8px 2px white;
}

.playlist{
  background-color:rgb(49, 37, 37);
  box-shadow: 0px 0px 8px 2px white inset;
  color: white;
  height:100vh;
  width:18vw;
}
.footer-copyright,nav{
  box-shadow: 0px 0px 8px 2px white inset;
  background-color:rgb(49, 37, 37);
  color: white;
}
#footer {
  /* position: fixed; */
  left: 0;
  bottom: 0;
  width: 100%;
}
.active{
  border-bottom: 3px solid rgb(112, 112, 168);
  font-weight: bold;
}
.card h5,p{
  text-shadow: none;
}

